.field-inline {
    & div.ra-field > div {
        width: 100%;
        flex-direction: row;
        margin-top: 0;
        margin-bottom: 0;

        & label {
            margin-bottom: 0;
            padding: 0;
            transform: none;
            line-height: inherit
        }

        & > div {
            width: auto;
            padding: 0;
            margin-left: 1rem;
            transform: none
        }
    }
}